import React , {useState} from 'react'
import "./Nav.css"
import {AiOutlineHome} from "react-icons/ai"
import {AiOutlineUser} from "react-icons/ai"
import {BiBook} from "react-icons/bi"
import {RiServiceLine} from "react-icons/ri"
import {BiMessageSquareDetail} from "react-icons/bi"

const Nav = () => {

    const [activeNav , setActiveNav] = useState("#");
    return (
      <nav>
        <a href="#"  onClick={() => setActiveNav('#') } className={activeNav === '#' ? 'active' : ''  }><AiOutlineHome/></a>
        <a href="#services" onClick={() => setActiveNav('#services') } className={activeNav === '#services' ? 'active' : ''  }><AiOutlineUser/></a>
        <a href="#industries" onClick={() => setActiveNav('#industries') } className={activeNav === '#industries' ? 'active' : ''  }><BiBook/></a>
        <a href="#articales" onClick={() => setActiveNav('#articales') } className={activeNav === '#articales' ? 'active' : ''  }><BiMessageSquareDetail/></a>
        <a href="#about" onClick={() => setActiveNav('#about') } className={activeNav === '#about' ? 'active' : ''  }><RiServiceLine/></a>
      </nav>
    )
  }
  
  export default Nav